body {
  font-family: PT Sans, sans-serif;
}

.container {
  width: 960px;
  padding: 0 15px;
  margin: 0 auto;
}

blockquote {
  margin: 0px 0px 15px 0px;
  background: #f7f7f7;
  border-left: solid 5px #ff0000;
  padding: 15px 20px;
  font-family: "Lato", Helvetica, Arial, sans-serif;
  font-size: 15px;
  font-style: italic;
  font-weight: 400;
  color: #ff0000;
}

.page__link {
  color: rgb(0, 128, 128);
}

.page__list {
  list-style: none;
  padding-left: 0;
}
.page__list li:before {
  content: "-";
  color: rgb(0, 128, 128);
  font-size: 1.2em;
  padding: 0 10px;
}

.page__signature {
  margin: 20px 0;
}

.page__heading {
  font-size: 1.4em;
  color: #4986ad;
}

.page__heading-main {
  margin: 40px 0;
  text-transform: uppercase;
  text-align: center;
  color: #009cc9;
  font-size: 1.4em;
  font-weight: 700;
}

.page__heading-italic {
  font-size: 1.6em;
  color: #008080;
}

.page__text {
  text-align: justify;
}

.page__signature-wrap {
  display: flex;
  justify-content: end;
}

.page__signature-image {
  float: right;
}

.page__frame {
  margin: 10px 0;
}

.page__text-center {
  text-align: center;
}

.page__cargo-cards {
  display: flex;
  justify-content: space-between;
  list-style: none;
  padding-left: 0;
}

.cargo-cards__item {
  border: 1px solid #CCC;
  border-radius: 5px;
  padding: 15px;
  transition: 0.2s ease-in-out;
}
.cargo-cards__item:hover {
  box-shadow: -5px 10px 15px rgba(0, 0, 0, 0.25);
  border-bottom-left-radius: 50% 20px;
}

.page__about {
  margin: 20px 0;
  border-radius: 3px;
  border: 1px solid #dbdbdb;
  padding: 10px;
  background-color: #f8f6f6;
}

.presintation-wrap {
  display: flex;
  justify-content: space-around;
}

.page__sertification-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 50px;
}

.page__image-wrap {
  display: flex;
  justify-content: center;
  margin: 30px 0;
}

.cost-cards {
  display: flex;
  justify-content: space-between;
  padding-left: 0;
  list-style: none;
}
.cost-cards__item {
  background-color: #009cc9;
  min-height: 150px;
  min-width: 150px;
  padding: 5px;
  border-radius: 10px;
  box-shadow: inset 0 1px hsla(0deg, 0%, 100%, 0.2), 0 3px 5px rgba(0, 1, 6, 0.5), 0 0 1px 1px rgba(0, 1, 6, 0.2);
  transition: 0.5s ease-in-out;
}
.cost-cards__item:hover {
  background: linear-gradient(#005978, #1a8dbc) #166c8b;
}

.oh-god {
  display: flex;
  justify-content: center;
  gap: 50px;
  margin: 30px 0;
}

.page__button {
  display: inline-block;
  width: 100%;
  height: 2.5em;
  line-height: 2.4em;
  vertical-align: middle;
  text-align: center;
  text-decoration: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  outline: none;
  border: 1px solid hsla(203deg, 8%, 47%, 0.8);
  border-top-color: rgba(0, 0, 0, 0.3);
  border-radius: 5px;
  background: #01b4df linear-gradient(#017e9e, #01aed9);
  box-shadow: inset 0 -1px rgba(10, 21, 28, 0.9), inset 0 1px hsla(0deg, 0%, 100%, 0.5);
  color: #fff;
  font-size: 1.2em;
  text-transform: uppercase;
  font-weight: 700;
  transition: 0.2s ease-in;
}
.page__button:hover {
  background: #cedce7 linear-gradient(#cedce7, #596a72);
}